/* Reset geral */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Corpo */
body {
  background-color: #ffffff;
  font-family: 'Arial', sans-serif;
  color: black;
  height: 100%;
  margin: 0;
}

/* Container principal */
.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
}

/* Cabeçalho */
.header {
  margin-top: 20px;
}

.title {
  font-size: 70px;
  color: #ff0000;
  text-shadow: 0 0 15px #ff0000, 0 0 30px #ff0000, 0 0 40px #00ff00;
  font-weight: bold;
  animation: brilho 1.5s infinite alternate;
}

@keyframes brilho {
  0% {
    text-shadow: 0 0 25px #ff0000, 0 0 50px #ff0000;
  }
  100% {
    text-shadow: 0 0 40px #00ff00, 0 0 60px #00ff00;
  }
}

.countdown {
  font-size: 30px;
  color: #00ff00;
  margin-top: 10px;
  animation: glow 1.5s infinite alternate;
}

@keyframes glow {
  0% {
    text-shadow: 0 0 20px #00ff00, 0 0 30px #00ff00;
  }
  100% {
    text-shadow: 0 0 40px #00ff00, 0 0 60px #00ff00;
  }
}

/* Conteúdo principal */
.main-content {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  padding: 20px;
  width: 100%;
  max-width: 1200px;
}

/* Lado esquerdo - Árvore de Natal, Papai Noel e Neve */
.left-section {
  position: relative;
  width: 45%;
}

.tree-container {
  text-align: center;
}

.tree {
  width: 70%;
  animation: bounce 1.5s infinite alternate;
}

.santa {
  width: 25%;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  animation: moveSanta 4s infinite alternate;
}

@keyframes moveSanta {
  0% {
    bottom: 0;
  }
  100% {
    bottom: 30px;
  }
}

@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-10px);
  }
}

/* Neve caindo */
.snowflakes {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
}

.snowflake {
  font-size: 30px;
  position: absolute;
  top: -50px;
  left: 50%;
  transform: translateX(-50%);
  animation: snow 8s linear infinite;
}

@keyframes snow {
  0% {
    top: -50px;
    opacity: 0.8;
  }
  100% {
    top: 100%;
    opacity: 0;
  }
}

/* Lado direito - Sugestões */
.right-section {
  background-color: rgba(73, 29, 29, 0.7);
  width: 45%;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 0px 4px 20px rgba(66, 30, 30, 0.4);
}

.right-section h2 {
  font-size: 28px;
  color: #521f1f;
  margin-bottom: 20px;
}

.suggestion-form {
  display: flex;
  flex-direction: column;
}

.suggestion-textarea {
  padding: 15px;
  font-size: 16px;
  border-radius: 10px;
  border: none;
  background-color: #f0f0f0;
  margin-bottom: 20px;
}

.submit-btn {
  padding: 10px;
  background-color: #ff0000;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  color: white;
  cursor: pointer;
  transition: all 0.3s ease;
}

.submit-btn:hover {
  background-color: #00ff00;
  transform: scale(1.05);
}
